footer {
  margin-top: 50px;
  height: 60px;
  display: grid;
  place-items: center;
  background-color: #d9d9d9;
}

footer p {
  font-weight: 300;
  color: black;
  font-size: 2.6rem;
}

footer p a {
  font-weight: 600;
  text-decoration: none;
  color: black;
}

footer p a:hover {
  font-weight: 300;
}
